<template lang="pug">
  div#information
    div.container
      div.information__text
        h1.information__text--title
          | {{ $t('generic.title') }}
        h3.information__text--subtitle
          | {{ serviceKo }}       .
          <br>
          |     ,         .
</template>

<script>
import {globalVar} from '@/globalVar'

export default {
  name: 'information',

  data () {
    return {
      serviceKo: globalVar.serviceKo,
    }
  },

}
</script>

<style lang="scss">

#information {
  padding-left: $grid8x;
  padding-right: $grid8x;
  background-color: $brand;
  border-top: 1px solid $texteee;

  .information__text {
    width: 100%;
    color: #fff;
    margin: 0 auto;
    text-align: center;

    .information__text--subtitle {
      margin: 0 auto;
      max-width: 560px;
      // font-weight: 400;
      @include font-size($grid5x);
    }
  }
}
</style>



